@mixin placeholder {
    &::-webkit-input-placeholder {
        @content;
    }

    &:-moz-placeholder {
        @content;
    }

    &::-moz-placeholder {
        @content;
    }

    &:-ms-input-placeholder {
        @content;
    }
}

@mixin layout {
    content: '';
    clear: both;
    display: block;
}

@mixin absolcenter {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@mixin absoltopcenter {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

@mixin absoldefault {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

@mixin unselect {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

@mixin margin-auto {
    margin-left: auto;
    margin-right: auto;
}

@mixin imgfit {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center top;
}
